* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 8px;
}

.country-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country__item {
  opacity: 0;
  align-items: center;
  animation: showCountry .25s cubic-bezier(.165, .84, .44, 1) forwards;
  display: flex;
  transform: translateX(-100%);
}

.country-info > div {
  align-items: center;
  display: flex;
}

.country-info p {
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: 24px;
}

.country-info p span {
  font-weight: 500;
}

.country-info > * {
  opacity: 0;
  animation: showCountryInfo .5s cubic-bezier(.39, .575, .565, 1) forwards;
  transform: translateY(-100%);
}

@keyframes showCountry {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes showCountryInfo {
  0% {
    opacity: 0;
    transform: translateY(-100%);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

